___CSS_LOADER_EXPORT___.push([module.id, ".required-marker legend:after{content:\"*\";color:red;margin-left:3px}", "",{"version":3,"sources":["webpack://./resources/js/components/visitors/RegisterVisitorForm.vue"],"names":[],"mappings":"AA8RA,8BACA,WAAA,CACA,SAAA,CACA,eACA","sourcesContent":["<template>\n    <validation-observer\n        ref=\"observer\"\n        v-slot=\"{ handleSubmit }\"\n    >\n        <b-form @submit.stop.prevent=\"handleSubmit(onSubmit)\">\n            <b-form-row>\n                <b-col sm>\n                    <validation-provider\n                        :name=\"$t('app.first_name')\"\n                        vid=\"first_name\"\n                        :rules=\"{ required: true }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.first_name')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                            class=\"required-marker\"\n                        >\n                            <b-form-input\n                                v-model=\"formData.first_name\"\n                                trim\n                                ref=\"firstNameInput\"\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n                <b-col sm>\n                    <validation-provider\n                        :name=\"$t('app.last_name')\"\n                        vid=\"last_name\"\n                        :rules=\"{ required: true }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.last_name')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                            class=\"required-marker\"\n                        >\n                            <b-form-input\n                                v-model=\"formData.last_name\"\n                                trim\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n            </b-form-row>\n            <b-form-row>\n                <b-col cols=\"auto\" class=\"pr-3\">\n                    <b-form-group :label=\"$t('app.type')\">\n                        <b-form-radio-group\n                            v-model=\"formData.type\"\n                            :options=\"types\"\n                            stacked\n                            @change=\"changeType\"\n                        />\n                    </b-form-group>\n                </b-col>\n                <b-col\n                  v-if=\"formData.type == 'visitor'\"\n                  sm\n                >\n                    <validation-provider\n                        :name=\"$t('app.id_number')\"\n                        vid=\"id_number\"\n                        :rules=\"{ }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.id_number')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                        >\n                            <b-form-input\n                                ref=\"idNumberInput\"\n                                v-model=\"formData.id_number\"\n                                trim\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n                <b-col\n                  v-if=\"formData.type == 'visitor'\"\n                  sm\n                >\n                    <validation-provider\n                        :name=\"$t('app.place_of_residence')\"\n                        vid=\"place_of_residence\"\n                        :rules=\"{ }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.place_of_residence')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                        >\n                            <b-form-input\n                                v-model=\"formData.place_of_residence\"\n                                trim\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n                <b-col\n                  v-if=\"formData.type == 'participant'\"\n                  sm\n                >\n                    <validation-provider\n                        :name=\"$t('app.activity_program')\"\n                        vid=\"activity\"\n                        :rules=\"{ }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.activity_program')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                        >\n                            <b-form-input\n                                ref=\"activityInput\"\n                                v-model=\"formData.activity\"\n                                trim\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n                <b-col\n                  v-if=\"formData.type == 'staff' || formData.type == 'external'\"\n                  sm\n                >\n                    <validation-provider\n                        :name=\"$t('app.organization')\"\n                        vid=\"organization\"\n                        :rules=\"{ }\"\n                        v-slot=\"validationContext\"\n                    >\n                        <b-form-group\n                            :label=\"$t('app.organization')\"\n                            :state=\"getValidationState(validationContext)\"\n                            :invalid-feedback=\"validationContext.errors[0]\"\n                        >\n                            <b-form-input\n                                ref=\"organizationInput\"\n                                v-model=\"formData.organization\"\n                                trim\n                                autocomplete=\"off\"\n                                :state=\"getValidationState(validationContext)\"\n                                :disabled=\"isDisabled\"\n                            />\n                        </b-form-group>\n                    </validation-provider>\n                </b-col>\n            </b-form-row>\n            <p>\n                <b-button\n                    variant=\"primary\"\n                    type=\"submit\"\n                    :disabled=\"isDisabled\"\n                >\n                    <font-awesome-icon icon=\"check\" />\n                    {{ $t('app.register') }}\n                </b-button>\n                <b-button\n                    variant=\"secondary\"\n                    type=\"button\"\n                    :disabled=\"isDisabled\"\n                    @click=\"reset\"\n                >\n                    <font-awesome-icon icon=\"eraser\" />\n                    {{ $t('app.reset') }}\n                </b-button>\n            </p>\n        </b-form>\n    </validation-observer>\n</template>\n\n<script>\nimport formInputMixin from '@/mixins/formInputMixin'\nexport default {\n    props: {\n        disabled: Boolean,\n    },\n    mixins: [ formInputMixin ],\n    data () {\n        return {\n            formData: this.initialFormData(),\n            types: [\n                { value: 'visitor', text: this.$t('app.visitor') },\n                { value: 'participant', text: this.$t('app.participant') },\n                { value: 'staff', text: this.$t('app.volunteer_staff') },\n                { value: 'external', text: this.$t('app.external_visitor') },\n            ]\n        }\n    },\n    computed: {\n        isDisabled () {\n            return this.disabled\n        }\n    },\n    watch: {\n        disabled (val, oldVal) {\n            if (!val && oldVal) {\n                this.$nextTick(() => this.selectFirstName())\n            }\n        }\n    },\n    mounted () {\n        this.focus()\n    },\n    methods: {\n        submit () {\n            this.$refs.observer.validate()\n                .then(valid => {\n                    if (valid) {\n                        this.onSubmit()\n                    }\n                })\n        },\n        onSubmit () {\n            if (this.formData.type == 'visitor') {\n                this.formData.activity = ''\n                this.formData.organization = ''\n            } else if (this.formData.type == 'participant') {\n                this.formData.id_number = ''\n                this.formData.place_of_residence = ''\n                this.formData.organization = ''\n            } else if (this.formData.type == 'staff' || this.formData.type == 'external') {\n                this.formData.id_number = ''\n                this.formData.place_of_residence = ''\n                this.formData.activity = ''\n            }\n            this.$emit('submit', this.formData)\n        },\n        reset () {\n            this.formData = this.initialFormData()\n            this.$refs.observer.reset()\n            this.focus()\n        },\n        initialFormData () {\n            return {\n                first_name: '',\n                last_name: '',\n                type: 'visitor',\n                id_number: '',\n                place_of_residence: '',\n                activity: '',\n                organization: ''\n            }\n        },\n        focus () {\n            this.$refs.firstNameInput.focus()\n        },\n        selectFirstName () {\n            this.$refs.firstNameInput.select()\n        },\n        changeType (value) {\n            if (value == 'staff' || value == 'external') {\n                this.$nextTick(() => this.$refs.organizationInput.focus())\n            } else if (value == 'participant') {\n                this.$nextTick(() => this.$refs.activityInput.focus())\n            } else {\n                this.$nextTick(() => this.$refs.idNumberInput.focus())\n            }\n        }\n    }\n}\n</script>\n\n<style>\n    .required-marker legend:after {\n        content: '*';\n        color: red;\n        margin-left: 3px;\n    }\n</style>\n"],"sourceRoot":""}]);
